<template>
	<div class="welcomeMain">
    <h1>{{msg}}</h1>
    <div id="allmap" class="allmap" style="height:400px;"></div>
    <h2 style="background: #ccc;">{{address}}</h2>
  </div>

</template>

<script>
	export default{
		data(){
			return{
				msg:"",
        address:""
			}
		},
    mounted: function () {

        this.getAddress('121.529079','31.272807');
        this.ready();
    },
    methods:{
      ready: function() {
          var map = new BMap.Map("allmap");
          var point = new BMap.Point(116.331398,39.897445);
          map.centerAndZoom(point,12);

          var geolocation = new BMap.Geolocation();
          geolocation.getCurrentPosition(function(r){
            if(this.getStatus() == BMAP_STATUS_SUCCESS){
              var mk = new BMap.Marker(r.point);
              map.addOverlay(mk);
              map.panTo(r.point);
              // alert('您的位置：'+r.point.lng+','+r.point.lat);
            }
            else {
              alert('failed'+this.getStatus());
            }
          },{enableHighAccuracy: true})
      },
      getAddress(longitude,latitude){

        var self=this;
        //通过baiduMap API获取街道名称
        var map = new BMap.Map("allmap");
        var point = new BMap.Point(longitude,latitude);
        var gc = new BMap.Geocoder();

        gc.getLocation(point, function(rs){
           var addComp = rs.addressComponents;

            /*
                city:城市
                district:辖区
                province:省会
                street:街道
                streetNumber:门牌号码
            */

           self.address ="位置："+ addComp.province + ", " + addComp.city+ ", " + addComp.district+ "," + addComp.street + ","  + addComp.streetNumber+"号";
        });
        // return address;

      },
    }
	}
</script>

<style scoped="scoped">
.welcomeMain{
	width: 100%;
	height: 100%;
	/*margin-top: -19px;*/
	background: url(../../../common/img/anjiLog1.jpg) no-repeat;
	background-size: 100% 100%;
}
</style>



